<template>
  <div class="icons">
    <el-row type="flex"
            style="flex-wrap:wrap">
      <el-col :xs="0"
              :sm="0"
              :md="0"
              :lg="2"
              :xl="2"
              class="left hidden-xs-only hidden-sm-only">
        <div v-for="(item,index) in ['fas','far','fab']"
             :key="index">
          <input type="checkbox"
                 :value="item"
                 v-model="checkedNames">
          <span style="width:1rem;display:inline-block;margin-left:0.2rem">
            {{item}}
          </span>
        </div>
        <!-- <div>选择了:{{ checkedNames}}</div> -->
      </el-col>
      <el-col :xs="24"
              :sm="24"
              :md="24"
              :lg="22"
              :xl="22"
              class="right">
        <el-row v-for="(item,index) in iconsList"
                :key="index+'iconsList'"
                type="flex"
                class="three-list">
          <!-- <label>{{item.title}}</label> -->
          <el-col :xs="12"
                  :sm="12"
                  :md="9"
                  :lg="6"
                  :xl="3"
                  v-for="(item2,index2) in item.list"
                  class="children-list"
                  :key="index2+'inde2'">
            <div style="margin-left:1rem;font-size:10px;"
                 v-clipboard:copy="item2"
                 v-clipboard:success="onCopy"
                 v-clipboard:error="onError">
              <!-- <img v-lazy="require('../../assets/logo.png')" /> -->
              <font-awesome-icon :icon="['fas', 'copy']"
                                 v-lazy />
            </div>
            <span class="item-icon">
              <font-awesome-icon v-if="item.title ==='fas'"
                                 :icon="['fas', item2]" />
              <font-awesome-icon v-if="item.title ==='far'"
                                 :icon="['far', item2]" />
              <font-awesome-icon v-if="item.title ==='fab'"
                                 :icon="['fab', item2]" />
            </span>
            <div class="item-text">{{item2}}</div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { far } from './far.js'
import { fab } from './fab.js'
import { fas } from './fas.js'
export default {
  data () {
    return {
      farList: far,
      fabList: fab,
      fasList: fas,
      iconsList: [],
      checkedNames: []
    }
  },
  beforeCreate () {

  },
  created () {
    if (JSON.parse(localStorage.getItem('iconsList'))) {
      this.iconsList = JSON.parse(localStorage.getItem('iconsList'))
    } else {
      this.iconsList = [{ 'title': 'fas', 'list': fas }, { 'title': 'far', 'list': far }, { 'title': 'fab', 'list': fab }]
      localStorage.setItem('iconsList', JSON.stringify(this.iconsList))
    }
  },
  watch: {
    checkedNames () {
      console.log(this.checkedNames)
      this.checkedNames.map((ele, index) => {
        console.log(ele)
        switch (ele) {
          case 'fas':
            // 隐藏另外两个
            this.iconsList = JSON.parse(localStorage.getItem('iconsList')).slice(-1)
            console.log(JSON.parse(localStorage.getItem('iconsList')).slice(-3))
            console.log(this.iconsList)
            break
          case 'far':
            // 隐藏另外两个
            this.iconsList = JSON.parse(localStorage.getItem('iconsList')).slice(0, 1)
            console.log(JSON.parse(localStorage.getItem('iconsList')).slice(0, 1))
            break
          case 'fab':
            // 隐藏另外两个
            this.iconsList = JSON.parse(localStorage.getItem('iconsList')).slice(1, 2)
            console.log(JSON.parse(localStorage.getItem('iconsList')).slice(1, 2))
            break
        }
      })
    }
  },
  methods: {
    onCopy () {
      this.$message({
        message: '恭喜你，复制成功',
        type: 'success'
      })
    },
    onError () {
      this.$message.error('复制出错了哦')
    },
    clickCheckbox (title) {
      console.log(title)
      console.log(this.checkedNames)
      switch (title) {
        case 'fas':
          // 隐藏另外两个
          this.iconsList = this.iconsList.slice(0, 0)
          break
        case 'far':
          // 隐藏另外两个
          this.iconsList = this.iconsList.slice(0, 1)
          break
        case 'fab':
          // 隐藏另外两个
          this.iconsList = this.iconsList.slice(1, 2)
          break
      }
    }
  }
}
</script>
<style lang="less" scoped>
.icons {
  font-size: 20px;
  .left {
    // background-color: red;
    display: flex;
    align-items: center;
    margin-top: 1rem;
    flex-direction: column;
  }
  .right {
    // background-color: green;
    padding: 0.5rem;
    .three-list {
      flex-wrap: wrap;
      margin: 10px 0;
      // background-color: pink;
      .children-list {
        // background-color: red;
        padding: 10px;
        border: 1px solid #fff;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .item-icon {
          font-size: 30px;
        }
        .item-text {
          font-size: 16px;
          margin-top: 2px;
          color: #adb5bd;
        }
        &:hover {
          background-color: #4dabf7;
          color: #fff !important;
        }
      }
    }
  }
}
</style>
